module.exports = [{"file":"README.md","lang":"md","section":"default","code":"<h2 id=\"install\">Install</h2>\n<p>You can use either the npm or yarn command-line tool to install packages.\nUse whichever is appropriate for your project in the examples below.</p>\n<h4 id=\"npm\">NPM</h4>\n<pre class=\"lang-shell\"><code class=\"lang-shell\">npm install --save @tdm/ngx-dynamic-forms @tdm/tixin @tdm/core\n</code></pre>\n<h4 id=\"yarn\">Yarn</h4>\n<pre class=\"lang-shell\"><code class=\"lang-shell\">yarn add @tdm/ngx-dynamic-forms @tdm/tixin @tdm/core\n</code></pre>\n<div class=\"info\">\n  The library depends on 2 libraries from the <code>@tdm</code> framework.\n</div>\n\n<h2 id=\"setup-dynamicformsmodule-\">Setup <code>DynamicFormsModule</code></h2>\n<p>We&#39;re building a new app, our root module is called <code>DynamicFunAppModule</code></p>\n<pre class=\"lang-ts\"><code class=\"lang-ts\">  <span class=\"hljs-keyword\">import</span> { NgModule } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@angular/core'</span>;\n  <span class=\"hljs-keyword\">import</span> { DynamicFormsModule } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@tdm/ngx-dynamic-forms'</span>;\n\n  <span class=\"hljs-meta\">@NgModule</span>({\n    ...\n    imports: [\n      DynamicFormsModule.forRoot( <span class=\"hljs-comment\">/* FORM CONTROL RENDERER */</span> )\n    ],\n    ...\n  })\n  <span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">class</span> DynamicFunAppModule { }\n}\n</code></pre>\n<p>We imported <code>DynamicFormsModule</code> using the <code>DynamicFormsModule.forRoot</code>,\nthis method will return a module with all services setup for use.</p>\n<p><code>DynamicFormsModule.forRoot</code> accepts 1 parameter, a component class,\nwhich is the default form control renderer. It is commented out right now\nwe will get to it in the next step.</p>\n<div class=\"alert\">\n  Using the <code>forRoot</code> is mandatory when registering our root module and\n  forbidden in child modules.\n</div>","title":"Setup"}]


